@media only screen and (max-width: 600px) {
.buttons {
  flex-direction: column-reverse;
  }
    #newQuote{
    margin-bottom: 10px;
  }
}

.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.buttons{
  display: flex;
  justify-content: space-between;
  min-height: 50px;
}

#pooh-pic {
  height: 260px;
  width: auto;
}

#text{
  margin-bottom: 0px;
}

#author{
  margin-top: 0;
  text-align: end;
}

#quote-box{
  padding: 25px;
  background-color: white;
  border-radius: 10px;
  color: black;
}

#tweet-quote{
  text-decoration: none;
  font-size: 16px;
  max-width: 100px;
  height: 100%;
  float: left;
  padding: 10px;
  color: white;
  border-radius: 10px;
  align-self: center;
  justify-content: center;
}

#new-quote{
  font-size: 16px;
  float: right;
  color: white;
  border-radius: 10px;
  padding: 10px;
  border: none;
  cursor: pointer;
}

#author{
  text-align: end;
}
